
.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.authentication {
  margin-block: 2rem;
}

.authentication > h2 {
  font-size: 15px;
  font-weight: 300;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 184px;
  height: 42px;
  margin: 1.5rem;
  margin-inline: auto;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  border: 1px solid #494949;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  color: #fff;
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
  margin: 0px;
}

.btn-text b {
  font-size: 13px;
  font-weight: 300;
  letter-spacing: 0.2px;
}

.privacy-policy {
  position: absolute;
  bottom: 0px;
  margin-bottom: 10px;
  font-size: 15px;
  font-weight: 300;
}
